@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.container {
  position: relative;
  width: 320px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 660px) {
  .container {
    width: 660px;
  }
}
@media (min-width: 940px) {
  .container {
    width: 940px;
    padding: 0;
  }
}
.page {
  font-family: "Open Sans", "Arial", sans-serif;
  height: 100%;
}
.page__body {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
}
@media (min-width: 940px) {
  .page-header {
    background-image: url("../img/back-desktop.jpg");
