  height: 23px;
  padding: 0;
  cursor: pointer;
  background-color: #283645;
  border: none;
  outline: none;
}
.page-header__toggle-btn::before {
  content: "";
  position: absolute;
  width: 2px;
  height: 30px;
  background-color: #ffffff;
  top: -3px;
  left: 10px;
  transform: rotate(45deg);
}
.page-header__toggle-btn::after {
  content: "";
  position: absolute;
  width: 2px;
  height: 30px;
  background-color: #ffffff;
  top: -3px;
  left: 10px;
  transform: rotate(-45deg);
}
